import Vue from 'vue'
import Router from 'vue-router'
import Things from '@/components/Things'
import NewThing from '@/components/NewThing'
import ViewThing from '@/components/ViewThing'
import EditThing from '@/components/EditThing'
import Categories from '@/components/Categories'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Things',
      component: Things
    },
    {
      path: '/categories',
      component: Categories
    },
    {
      path: '/new',
      name: 'new-thing',
      component: NewThing
    },
    {
      path: '/edit/:thing_id',
      name: 'edit-thing',
      component: EditThing
    },
    {
      path: '/:thing_id',
      name: 'view-thing',
      component: ViewThing
    }
  ]
})



